<template>
  <div class="app-detail-page" v-loading="loading">
    <div class="left">
      <header class="header">
        <div class="app-info">
          <div class="app-icon">
            <img :src="getImageUrl(info.icon)" />
          </div>
          <div class="app-content">
            <div class="app-name">{{ info.appName }}</div>
            <div class="app-content-bottom">
              <div class="app-tag">{{ info.categoryName }}</div>
              <div class="app-public-info">
                <span
                  >创建时间 :
                  {{ $format('yyyy-MM-dd', info.publishTime) }}</span
                >
                <span>创建人: {{ info.publishUser }}</span>
                <span>版本: {{ info.version }}</span>
              </div>
            </div>
          </div>
          <div class="app-button-group">
            <el-button @click="goToAppList" size="mini"
              ><i class="el-icon-arrow-left"></i>返回</el-button
            >
            <el-button
              v-if="info.userInstallStatus === 1"
              class="opt-btn"
              size="mini"
              @click.stop="installItemApp(info)"
              >一键安装</el-button
            >

            <el-button
              v-else-if="info.userInstallStatus === 2"
              class="opt-btn"
              type="primary"
              size="mini"
              @click.stop="useApp(info)"
              >访问</el-button
            >
            <el-button
              v-else-if="info.userInstallStatus === 3"
              class="opt-btn"
              type="primary"
              size="mini"
              @click.stop="upgrade(info.appId)"
              >升级</el-button
            >
          </div>
        </div>
      </header>
      <main class="main">
        <div class="cover">
          <el-carousel
            :interval="5000"
            height="350px"
            indicator-position="outside"
            arrow="never"
          >
            <el-carousel-item
              v-for="item in info.screenshot"
              :key="item"
              :style="`background-image: url(${getImageUrl(item)})`"
            >
            </el-carousel-item>
          </el-carousel>
        </div>
        <div class="desc">
          <div class="desc-title">应用描述</div>
          <div class="desc-content">{{ info.description || '该开发者很懒，没有填写应用描述' }}</div>
        </div>
      </main>
    </div>
    <div class="hot-list">
      <div class="title">热门应用</div>
      <div class="list">
        <div class="list-item"></div>
        <div class="empty-tips">暂无热门应用</div>
      </div>
    </div>
  </div>
</template>

<script>
//url
import * as API from '@/api/applicationMarket'

// func
import { getImageUrl } from '@/utils'
import appOperate from '../mixins/appOperate'
import { jumpPreview } from '@/utils/url'
import breadcrumbControl from '@/mixins/breadcrumb-control'
import { authKey } from '@/router'

export default {
  mixins: [appOperate, breadcrumbControl],
  props: {},
  data() {
    return {
      info: {},
      getImageUrl,
      id: this.$route.query.id,
    }
  },
  created() {
    this.getInfo()
  },
  mounted() {},
  methods: {
    goToAppList() {
      if (history.length > 2) {
        rootApp.$router.go(-2)
      } else {
        this.$router.replace({ name: authKey.myAppDesktop })
      }
    },
    getInfo() {
      API.appDetail({
        appId: this.id,
      }).then((res) => {
        this.info = res.data
        // this.getHotList()
      })
    },
    getHotList() {
      API.appHotList({
        pagenum: 0,
        pagesize: 0,
        sort: {
          name: '',
          type: this.info.categoryCode,
        },
      }).then((res) => {
        console.log(res)
      })
    },
    async upgrade(appId) {
      await this.appUpgrade(appId)
      this.getInfo()
    },
    async installItemApp(item) {
      await this.appInstall(item)
      this.getInfo()
    },
    // 初始化顶部面包屑和返回按钮
    initTopBar() {
      const routeKey = this.$route.path
      const paths = this.$store.state.appendPath
      !paths.find((item) => item.key === routeKey) &&
        this.$store.commit('pushAppendPath', {
          data: {
            label: '应用详情',
            back: () => {
              rootApp.$router.replace({ name: authKey.myAppDesktop })
            },
            click: () => {},
            hideBack: false,
            key: routeKey,
          },
        })
    },
  },

  computed: {},

  watch: {},
}
</script>
